<template>
  <select
    class="form-control c-select"
    v-model="selectedAgent"
    v-if="!loading"
  >
    <option
      value=""
      :disabled="!clearable"
      :hidden="!clearable"
    >
      {{ $t('common.please_select') }}
    </option>
    <option
      class="form-control"
      :value="a.username"
      v-for="a in agentList"
      :key="a.id"
    >{{ a.username }}
    </option>
  </select>
  <span
    class="form-control"
    v-else
  >
    <i class="fa fa-spin fa-spinner"></i>
  </span>
</template>

<script>
import api from '../api'
export default {
  props: {
    agent: {
      type: String,
      default: ''
    },
    clearable: {
      type: Boolean,
      default: false
    },
    hiddenList: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      agentList: [],
      selectedAgent: '',
      loading: true
    }
  },
  watch: {
    agent (newObj, old) {
      this.selectedAgent = newObj || ''
    },
    selectedAgent (newObj, old) {
      if (newObj) {
        this.$emit('agent-select', newObj)
      } else {
        this.$emit('agent-select', '')
      }
    }
  },
  created () {
    this.getAgentList()
  },
  methods: {
    getAgentList () {
      this.$http.get(`${api.manageUser}?roles=agent&opt_expand=1`).then(data => {
        // hide the agent and whose agent are in hidden-list
        this.agentList = data.filter(agent => !(this.hiddenList.includes(agent.username) || (agent.agent && this.hiddenList.includes(agent.agent.username))))
        this.selectedAgent = this.agent
        this.loading = false
      })
    }
  }
}
</script>



