<template>
  <select
    class="form-control w-sm c-select"
    v-model="selectedRole"
    v-if="mode === 'select'"
  >
    <option value="">{{$t('common.please_select')}}</option>
    <option
      class="form-control"
      :value="r.name"
      v-for="r in roleFilter"
      :key="r.id"
    >{{ r.display_name }}
    </option>
  </select>
  <div class="checkbox" v-else>
    <label
      class="m-r-sm"
      v-for="r in roleFilter"
      :key="r.id"
    >
      <input
        type="checkbox"
        name="role"
        v-model="checkboxRole[r.name]"
        :disabled="disabledList.includes(r.name)"
      >
      <i class="dark-white"></i>
      {{ r.display_name }}
    </label>
  </div>
</template>

<script>
export default {
  props: {
    role: {
      default: ''
    },
    mode: {
      default: 'select'
    },
    noShowList: {
      default: ''
    },
    disabledList: {
      default: ''
    }
  },
  data () {
    return {
      roles: [{
        display_name: this.$t('role.agent'),
        id: 3,
        name: 'agent'
      },
      {
        display_name: this.$t('role.customer_service'),
        id: 4,
        name: 'customer service'
      },
      {
        display_name: this.$t('role.manager'),
        id: 1,
        name: 'manager'
      },
      {
        display_name: this.$t('role.member'),
        id: 2,
        name: 'member'
      }],
      selectedRole: '',
      checkboxRole: {}
    }
  },
  watch: {
    role (newObj, old) {
      if (this.mode === 'select') {
        this.selectedRole = newObj || ''
      } else {
        this.role.forEach(element => {
          this.$set(this.checkboxRole, element, true)
        })
      }
    },
    selectedRole (newObj, old) {
      if (newObj) {
        this.$emit('role-select', newObj.toString())
      } else {
        this.$emit('role-select', '')
      }
    },
    checkboxRole: {
      handler (newObj) {
        this.$emit('role-select', Object.keys(newObj).filter(e => newObj[e]))
      },
      deep: true
    }
  },
  computed: {
    roleFilter () {
      return this.roles.filter(element => !this.noShowList.includes(element.name))
    }
  },
  created () {
    if (this.mode === 'select') {
      this.selectedRole = this.role
    } else {
      if (this.role && this.role.length) {
        this.role.forEach(element => {
          this.$set(this.checkboxRole, element, true)
        })
      }
    }
  }
}
</script>



