<template>
  <div>
    <div class="row m-r-xs m-l-xs m-b">
      <button
        class="pull-left md-btn blue w-sm"
        @click="openModal('create')"
      >
        {{ $t('action.create_object', { object: $t('common.member')}) }}
      </button>
    </div>

    <form class="form text-sm" v-on:submit.prevent="submit">
      <div class="box">
        <div class="box-body clearfix form-input-sm">
          <div class="row m-l-xs m-r-xs">
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.username_q}"
              >{{ $t('common.username') }}
              </label>
              <input
                v-model.trim="query.username_q"
                class="form-control w-sm"
                :placeholder="$t('common.username')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.nickname_q}"
              >{{ $t('common.nickname') }}
              </label>
              <input
                v-model.trim="query.nickname_q"
                class="form-control w-sm"
                :placeholder="$t('common.nickname')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.agent_q}"
              >{{ $t('common.agent') }}
              </label>
              <input
                v-model.trim="query.agent_q"
                class="form-control w-sm"
                :placeholder="$t('common.agent')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.roles}"
              >{{ $t('common.role') }}
              </label>
              <role
                style="display: block;"
                :role="query.roles"
                @role-select="roleFilter"
              />
            </div>
            <button
              class="md-btn pull-right btn m-t-md w-xs"
              type="button"
              @click="clearAll"
              :disabled="isQueryEmpty"
            >
              <span>
                <i class="fa fa-spin fa-spinner" v-if="loading"></i>
                <i class="fa fa-trash-o" v-else></i>
                 {{ $t('action.clear') }}
              </span>
            </button>
          </div>
        </div>
      </div>
    </form>

    <div class="box">
      <table st-table="rowCollectionBasic" class="table table-striped">
        <thead>
          <tr>
            <th style="width: 15%;">{{ $t('common.username') }}</th>
            <th>{{ $t('common.nickname') }}</th>
            <th>{{ $t('common.mobile') }}/{{ $t('common.QQ') }}</th>
            <th>{{ $t('common.agent') }}</th>
            <!-- <th class="text-center">{{ $t('common.online_period') }}</th> -->
            <th class="text-center">{{ $t('common.date_joined') }}</th>
            <th class="text-center">{{ $t('common.registered_ip') }}</th>
            <th class="text-center">{{ $t('common.role') }}</th>
            <th class="text-center" style="width: 10%;">{{ $t('common.operate') }}</th>
          </tr>
        </thead>
        <tbody v-if="queryset.length">
          <tr
            v-for="member in queryset"
            :key="member.id"
          >
            <td>{{ member.is_robot ? $t('setting.robot') : (member.username || '-')  }}</td>
            <td>{{ member.nickname || '-' }}</td>
            <td>
              <span>{{ member.mobile || '-' }}</span>
              <br/>
              <span>{{ member.QQ || '-' }}</span>
            </td>
            <td>
              <span v-if="member.agent">{{ member.agent.username || '-' }}</span>
              <span v-else>-</span>
            </td>
            <!-- <td class="text-center">
              <span v-if="member.last_login">{{ member.last_login | moment("YYYY-MM-DD HH:mm:ss") }}</span>
              <span v-else>{{ '-' }}</span>
            </td> -->
            <td class="text-center">
              <span v-if="member.date_joined">{{ member.date_joined | moment("YYYY-MM-DD HH:mm:ss") }}</span>
              <span v-else>{{ '-' }}</span>
            </td>
            <td class="text-center">{{ member.registered_ip || '-' }}</td>
            <td class="text-center" v-if="member.roles">
              <span
                v-for="(role, index) in member.roles"
                :key="role.id"
              >
                <br v-if="index > 0"/>{{ role.display_name }}
              </span>
            </td>
            <td class="text-center" v-else>-</td>
            <td class="text-center">
              <button
                class="btn btn-xs btn-icon blue"
                @click="openModal('update', Object.assign({}, member, {
                  roles: member.roles.map(element => element.name),
                  agent: member.agent.username || '',
                  hasAgent: !!member.agent.username,
                  agent_customer_services: member.agent_customer_services ? member.agent_customer_services.map(e => e.username) : [],
                  domain: member.domain || '',
                  password: '',
                  confirmation_password: ''
                }))"
              >
                <i class="fa fa-edit"></i>
              </button>
              <button
                class="btn btn-xs btn-icon danger"
                @click="openModal('delete', member)"
              >
                <i class="fa fa-trash-o"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="modal" v-if="modal.showModal">
      <div class="modal-backdrop fade in" @click="modal.showModal = false">
      </div>
      <form
        class="form"
        @submit.prevent="updateMember(
          modal.mode,
          Object.assign({}, modal.member, {
            agent_username: modal.member.agent,
            role_names: [...modal.member.roles]
          })
        )"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">
                {{ modal.mode && $t(`action.${modal.mode}_object`, { object: $t('common.member') }) }}
              </h5>
            </div>
            <div
              style="overflow: scroll; max-height:600px"
              class="modal-body p-lg"
              v-if="modal.mode !== 'delete'"
            >
              <div class="row">
                <div class="col-sm-12 text-center">
                  <h6>{{ $t('common.basic_info') }}</h6>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label p-r-0">{{ $t('common.username') }}</label>
                  <span class="text-danger">*</span>
                </div>
                <div class="col-sm-5">
                  <input
                    class="form-control"
                    v-model.trim="modal.member.username"
                    pattern="[A-Za-z\d]{6,15}"
                    :title="$t('message.username_form_validation_msg', {
                      digit: '6~15'
                    })"
                    required
                  />
                  <span class="m-l-xs text-xs text-muted">{{ $t('message.username_form_validation_msg', {
                    digit: '6~15'
                  }) }}
                  </span>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label p-r-0">{{ $t('common.nickname') }}</label>
                  <span class="text-danger">*</span>
                </div>
                <div class="col-sm-5">
                  <input
                    class="form-control"
                    v-model.trim="modal.member.nickname"
                    required
                  />
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label
                    class="form-control-label"
                    :class="{'p-r-0': modal.mode === 'create'}"
                  >{{ modal.mode === 'create' ? $t('common.password') : $t('action.reset_password') }}</label>
                  <span class="text-danger" v-if="modal.mode === 'create'">*</span>
                </div>
                <div class="col-sm-5">
                  <input
                    type="password"
                    class="form-control"
                    v-model.trim="modal.member.password"
                    @input="checkPasswordSame"
                    :required="modal.mode === 'create'"
                    pattern="(?=.*[A-Z])(?=.*\d)[A-Za-z\d]{8,15}"
                    :title="$t('message.password_form_validation_msg', {
                      digit: '8~15'
                    })"
                    autocomplete="off"
                  />
                  <span class="m-l-xs text-xs text-muted">{{ $t('message.password_form_validation_msg', {
                      digit: '8~15'
                    }) }}
                  </span>
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label
                    class="form-control-label"
                    :class="{'p-r-0': modal.mode === 'create'}"
                  >{{ $t('common.password_check') }}</label>
                  <span class="text-danger" v-if="modal.mode === 'create'">*</span>
                </div>
                <div
                  class="col-sm-5"
                  :class="{'has-warning': !modal.isPasswordSame}"
                >
                  <input
                    type="password"
                    class="form-control"
                    v-model.trim="modal.member.confirmation_password"
                    :class="{'form-control-warning': !modal.isPasswordSame}"
                    :required="modal.member.password.length > 0"
                    @input="checkPasswordSame"
                  />
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.mobile') }}</label>
                </div>
                <div class="col-sm-5">
                  <input
                    class="form-control"
                    v-model.trim="modal.member.mobile"
                  />
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.QQ') }}</label>
                </div>
                <div class="col-sm-5">
                  <input
                    class="form-control"
                    v-model.trim="modal.member.QQ"
                  />
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.agent') }}</label>
                </div>
                <div class="col-sm-5">
                  <agent
                    :clearable="!modal.member.hasAgent"
                    :hiddenList="[modal.member.username]"
                    :agent="modal.member.agent"
                    @agent-select="agentSelect"
                  />
                </div>
              </div>
              <div class="row m-t-xs">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.role') }}</label>
                </div>
                <div class="col-sm-6 m-t-sm">
                  <role
                    :role="modal.member.roles"
                    :mode="'checkbox'"
                    :noShowList="['member']"
                    :disabledList="modal.isAgent ? ['agent'] : []"
                    @role-select="roleSelect"
                  />
                </div>
              </div>
              <div class="row b-t m-t-sm p-t-sm" v-if="isChosingAgent">
                <div class="col-sm-12 text-center m-t-sm">
                  <h6>{{ $t('nav.agent_promo') }}</h6>
                </div>
              </div>
              <div class="row m-t-xs" v-if="isChosingAgent">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.promote_code') }}</label>
                </div>
                <div class="col-sm-5">
                  <label class="form-control-label p-l-0">{{ modal.member.promote_code }}</label>
                </div>
              </div>
              <div class="row m-t-xs" v-if="isChosingAgent">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('common.agent_domain') }}</label>
                </div>
                <div class="col-sm-5">
                  <input
                    class="form-control"
                    :placeholder="'\',\''"
                    v-model.trim="modal.member.domain"
                  />
                </div>
              </div>
              <div class="row m-t-xs" v-if="isChosingAgent">
                <div class="col-sm-3 col-sm-offset-1 text-right">
                  <label class="form-control-label">{{ $t('role.customer_service') }}</label>
                </div>
                <div class="col-sm-6 m-t-sm">
                  <customer-service
                    :customerService="modal.member.agent_customer_services"
                    @cs-select="csSelect"
                  />
                </div>
              </div>
            </div>
            <div class="modal-body p-lg" v-else>
              <span>{{ $t('message.sure_to_do', {
                object: `${$t('common.member')} (${modal.member.username})`,
                operate: $t('action.delete')
              }) }}
              </span>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn dark-white p-x-md w-xs"
                @click="modal.showModal = false">
                {{ $t('action.cancel') }}
              </button>
              <button
                type="submit"
                class="btn blue p-x-md w-xs"
                :disabled="!modal.isPasswordSame"
              >
                <span v-if="modal.loading"><i class="fa fa-spin fa-spinner"></i></span>
                <span v-else>{{ $t('action.confirm') }}</span>
              </button>
            </div>
          </div>
        </div>
      </form>
    </div>

    <pulling
      :api="api"
      :query="query"
      :queryset="queryset"
      :extra="'sort=date_joined'"
      @query-data="queryData"
      @query-param="queryParam"
      ref="pulling"
    />
  </div>
</template>
<script>
import Pulling from '../components/Pulling.vue'
import Role from '../components/Role.vue'
import Agent from '../components/Agent.vue'
import CustomerService from '../components/CustomerService.vue'
import api from '../api'
import $ from '../utils/util'
import _ from 'lodash'
export default {
  data () {
    return {
      api: api.manageUser,
      query: {},
      queryset: [],
      modal: {
        showModal: false,
        mode: '',
        member: {},
        isPasswordSame: true,
        loading: false
      },
      requiredFields: [
        'username',
        'nickname',
        'mobile',
        'QQ',
        'role_names'
      ],
      loading: true
    }
  },
  created () {
    this.setQueryAll()
    this.rebase()
  },
  watch: {
    '$route': {
      handler () {
        this.loading = true
        this.setQueryAll()
        this.queryset = []
        this.$refs.pulling.rebase()
      },
      deep: true
    }
  },
  computed: {
    isQueryEmpty () {
      return $.compareQuery(this.query, {})
    },
    isChosingAgent () {
      return this.modal.member.roles.includes('agent')
    }
  },
  methods: {
    setQueryAll () {
      this.query = Object.assign({}, this.$route.query)
    },
    getPromo () {
      return new Promise((resolve) => {
        this.$http.get(`${api.user}promote_info/`).then(data => {
          resolve(data)
        })
      })
    },
    openModal (mode = '', member = {
      username: '',
      nickname: '',
      password: '',
      confirmation_password: '',
      mobile: '',
      QQ: '',
      roles: ['member'],
      promote_code: '',
      agent: '',
      hasAgent: false,
      domain: '',
      agent_customer_services: []
    }) {
      this.modal = Object.assign(this.modal, {
        mode,
        member,
        isAgent: member.roles.includes('agent'),
        showModal: true,
        loading: false
      })
      if (mode === 'create' || (mode === 'update' && !member.promote_code)) {
        this.getPromo().then(data => {
          this.modal.member = Object.assign(member, data)
        })
      }
    },
    closeModal () {
      this.modal.showModal = false
    },
    updateMember (mode = '', member) {
      this.modal.loading = true
      let result = {}
      let fields = this.requiredFields
      if (mode === 'create' || (mode === 'update' && member.password)) {
        fields = [...fields, 'password', 'confirmation_password']
      }
      if (member.agent_username) {
        result['agent_username'] = member.agent_username
      }
      if (this.isChosingAgent) {
        fields = [...fields, 'domain', 'promote_code', 'agent_customer_services']
      }
      fields.forEach(key => {
        result[key] = member[key]
      })
      if (mode === 'create') {
        this.$http.post(this.api, result).then(data => {
          this.notify(mode, 'success', 'success', true, true)
        }, error => {
          this.notify(mode, 'danger', 'failed', false, false, error)
        })
      } else if (mode === 'update') {
        this.$http.put(`${this.api}${member.id}/`, result).then(data => {
          this.notify(mode, 'success', 'success', true, true)
        }, error => {
          this.notify(mode, 'danger', 'failed', false, false, error)
        })
      } else if (mode === 'delete') {
        this.$http.delete(`${this.api}${member.id}/`).then(data => {
          this.notify(mode, 'success', 'success', true, true)
        }, error => {
          this.notify(mode, 'danger', 'failed', false, false, error)
        })
      }
    },
    roleFilter (val) {
      this.query.roles = val
      this.submit()
    },
    agentSelect (val) {
      this.modal.member.agent = val
    },
    roleSelect (val) {
      this.modal.member.roles = val
    },
    csSelect (val) {
      this.modal.member.agent_customer_services = val
    },
    queryData (queryset) {
      this.loading = false
      this.queryset = queryset
    },
    queryParam (query) {
      this.query = Object.assign(this.query, query)
    },
    search:
      _.debounce(function () {
        this.submit()
      },
      700),
    checkPasswordSame:
      _.debounce(function () {
        this.modal.isPasswordSame = this.modal.member.password === this.modal.member.confirmation_password
      },
      700),
    submit () {
      if (!$.compareQuery(this.query, this.$route.query)) {
        this.$refs.pulling.submit()
      }
    },
    rebase () {
      this.$nextTick(() => {
        this.$refs.pulling.rebase()
      })
    },
    clearAll () {
      this.query = {}
      this.$nextTick(() => {
        this.submit()
      })
    },
    notify (operate = 'update', type = 'success', status = 'success', closeModal = false, rebase = false, addMsg = '') {
      this.modal.loading = false
      $.notify({
        message: this.$t('action.' + operate) + this.$t('status.' + status) + (addMsg && ` (${addMsg})`),
        type
      })
      closeModal && this.closeModal()
      rebase && this.rebase()
    }
  },
  components: {
    Pulling,
    Agent,
    Role,
    CustomerService
  }
}
</script>



