<template>
  <div class="checkbox" v-if="!loading">
    <label
      class="m-r-sm"
      v-for="cs in csList"
      :key="cs.id"
    >
      <input
        type="checkbox"
        name="cs"
        v-model="checkboxCS[cs.username]"
      >
      <i class="dark-white"></i>
      {{ cs.username }}
    </label>
  </div>
  <span v-else>
    <i class="fa fa-spin fa-spinner"></i>
  </span>
</template>

<script>
import api from '../api'
export default {
  props: {
    customerService: {
      default: ''
    }
  },
  data () {
    return {
      api: api.manageUser,
      csList: [],
      checkboxCS: {},
      loading: true
    }
  },
  watch: {
    customerService (newObj, old) {
      newObj.forEach(key => {
        this.$set(this.checkboxCS, key, true)
      })
    },
    checkboxCS: {
      handler (newObj) {
        this.$emit('cs-select', Object.keys(newObj).filter(key => newObj[key]))
      },
      deep: true
    }
  },
  created () {
    this.$http.get(`${this.api}?roles=customer service`).then(data => {
      this.csList = data.sort((a, b) => {
        if (this.customerService.includes(a.username)) return -1
        else return 1
      })
      this.loading = false
    })
    if (this.customerService && this.customerService.length) {
      this.customerService.forEach(key => {
        this.$set(this.checkboxCS, key, true)
      })
    }
  }
}
</script>



